/* CSS Variables - 全局变量定义 */
:root {
  /* 默认使用 light 主题变量，防止闪烁 */
  --surface: #ffffff;
  --surface-variant: #f7f8fa;
  --on-surface: #111827;
  --muted: #6b7280;
  --elevation-1: rgba(2,6,23,0.06);
  --elevation-2: rgba(2,6,23,0.08);
  
  /* 主题颜色 */
  --main-bg-color: var(--surface);
  --main-text-color: var(--on-surface);
  --secondary-text-color: var(--muted);
  --accent-color: #ffffff;
  --highlight-color: #61dafb;
  
  /* 字体 */
  --font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  --font-family-mono: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  
  /* 字体大小 */
  --font-size-xs: 0.75rem;   /* 12px */
  --font-size-sm: 0.875rem;  /* 14px */
  --font-size-base: 1rem;    /* 16px */
  --font-size-lg: 1.125rem;  /* 18px */
  --font-size-xl: 1.25rem;   /* 20px */
  --font-size-2xl: 1.5rem;   /* 24px */
  --font-size-3xl: 1.875rem; /* 30px */
  --font-size-4xl: 2.25rem;  /* 36px */
  --font-size-5xl: 3rem;     /* 48px */
  --font-size-6xl: 4rem;     /* 64px */
  
  /* 间距 */
  --spacing-xs: 0.25rem;   /* 4px */
  --spacing-sm: 0.5rem;    /* 8px */
  --spacing-md: 1rem;      /* 16px */
  --spacing-lg: 1.5rem;    /* 24px */
  --spacing-xl: 2rem;      /* 32px */
  --spacing-2xl: 3rem;     /* 48px */
  
  /* 边框圆角 */
  --border-radius-sm: 0.25rem;  /* 4px */
  --border-radius-md: 0.5rem;   /* 8px */
  --border-radius-lg: 1rem;     /* 16px */
  
  /* 阴影 */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);
  --shadow-mh: 3px 5px hsla(0, 0, 0, 0.75);
  
  /* 过渡动画 */
  --transition-fast: 0.15s ease;
  --transition-base: 0.3s ease;
  --transition-slow: 0.5s ease;
  
  /* Z-index */
  --z-index-dropdown: 100;
  --z-index-modal: 1000;
  --z-index-tooltip: 1100;
}

/*
 优化版本
  语义化主题变量（浅色/深色）
  - 首选使用语义 token（surface / on-surface / surface-variant / elevation-*）
  - 支持 prefers-color-scheme 和通过 html[data-theme] 覆盖
*/
:root {
  --surface: #ffffff;
  --surface-variant: #f7f8fa;
  --on-surface: #111827;
  --muted: #6b7280;
  --elevation-1: rgba(2,6,23,0.06);
  --elevation-2: rgba(2,6,23,0.08);
}

@media (prefers-color-scheme: dark) {
  :root {
    --surface: #0b0f14;
    --surface-variant: #0f1720;
    --on-surface: #f3f4f6;
    --muted: #9ca3af;
    --elevation-1: rgba(255,255,255,0.03);
    --elevation-2: rgba(255,255,255,0.04);
  }
}

/* 用户通过 data-theme 强制切换（优先于 prefers-color-scheme） */
html[data-theme="light"] {
  --surface: #ffffff;
  --surface-variant: #f7f8fa;
  --on-surface: #111827;
  --muted: #6b7280;
  --elevation-1: rgba(2,6,23,0.06);
  --elevation-2: rgba(2,6,23,0.08);
}

html[data-theme="dark"] {
  --surface: #282c34;
  --surface-variant: #0f1720;
  --on-surface: #f3f4f6;
  --muted: #9ca3af;
  --elevation-1: rgba(255,255,255,0.03);
  --elevation-2: rgba(255,255,255,0.04);
}

