.widgets-photo {
    width: 320px;
    height: 320px;
    border-radius: 22px;
    overflow: hidden; /* 关键：隐藏超出圆角的部分 */
    position: relative; /* 为绝对定位的子元素提供定位上下文 */
    display: flex;
    align-items: center;
    justify-content: center;
}

/* 加载占位符 - 绝对定位，显示在最顶层 */
.photo-placeholder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
    background-size: 200% 100%;
    animation: loading 1.5s infinite;
    z-index: 10; /* 确保在顶层 */
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* 缩略图样式 */
.photo-thumbnail {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(2px);
    transition: filter 0.3s ease;
    z-index: 5; /* 在加载占位符下方 */
    cursor: pointer;
}

/* 高清图样式 */
.photo-fullimage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 6; /* 在缩略图上方 */
    cursor: pointer;
}

.photo-fullimage.loaded {
    opacity: 1;
}

/* 动画效果 */
@keyframes loading {
    0% {
        background-position: 200% 0;
    }
    100% {
        background-position: -200% 0;
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}